#form-file-upload {
    margin: auto;
	width: 90%;
	height: 50%;
  }

#content{
    outline: 2px dashed #92b0b3;
    outline-offset:-10px;
	transition: all .15s ease-in-out;
	
    width: 100%;
	height: 100%;
    text-align: center;
	background-color: none;
	display: table;
}

#content.drag-active {
	background-color: rgba(0,0,0,0.8);
	outline-offset: -20px;
}

#input-file-upload {
	display: none;
  }

.text {
	display: table-cell;
    vertical-align: middle;
	text-align: center;
	padding-bottom: 25px;
}

@media (min-width: 1024px) {
	#form-file-upload {
		width: 800px;
	}

	#content{
		width: 800px;
	}
}

.btn:hover {
	background-image: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2));
}

#drag-file-element {
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: 1rem;
	top: 0px;
	right: 0px;
	bottom: 0px;
	left: 0px;
  }